import './About.css';
import face from "../assets/face.png";

import { Link } from "react-router-dom";
import { motion } from "framer-motion";

export default function About() {

    return (
        <>

        <div class="about-name">
            Hi! I'm <Link style={{ textDecoration: 'none' }} to="/"><span>Graham.</span></Link>
        </div>

        <div class="about-body">
            <p>I’m a computing student at Queen’s university, expecting to graduate in 2023! I really like making stuff, especially apps and websites. I like making nice looking UI, and nice looking code.</p>
            <p>I started making stuff back in highschool. Back then used notepad to hand-write html and css (I was an altruist). Since then I've graduated to working with html, css, <i>and javascript</i> (wow!). My favorite languages to work in are Javascript, React, or React Native, but I also like working with Python and Java!</p>
            <p>This website is still under construction. Check back later for more content!</p>
        </div>

        <div class="about-body">
            Get in{" "}
            <a 
                style={{ textDecoration: 'none' }} 
                href="https://www.linkedin.com/in/gcarkner/"
                target="_blank"
            >
                <span>touch</span>
            </a>.
        </div>

        <motion.img 
            class="face" 
            src={face} 
            initial="hidden"
            animate="visible"
            variants={{
                hidden: {
                    marginLeft: -400
                },
                visible: {
                    marginLeft: 0
                }
            }}
        />

        </>
    )
}
